import React, { Component } from "react";
import Button from "@material-ui/core/Button";
import Icon from "@material-ui/core/Icon";
import IconButton from "@material-ui/core/IconButton";
import { Toast } from "primereact/toast";
import { Dialog } from "primereact/dialog";
import "./categorie.css";

export default class Categorie extends Component {
  state = {
    showConfirmDialog: false,
    confirm_dialog_active: false,
    create_cat_dialog_active: false,
    editing: false,
  };
  categories = [
    "Electronique",
    "Electronique",
    "Electronique",
    "Electronique",
    "Electronique",
    "Electronique",
    "Electronique",
  ];
  ask_create_cath = () => {
    this.setState({
      create_cat_dialog_active: true,
      editing: false,
    });
  };

  ask_edit_cath = () => {
    this.setState({
      create_cat_dialog_active: true,
      editing: true,
    });
  };
  delete_cath = () => {
    this.setState({
      confirm_dialog_active: false,
    });
    // @ts-ignore
    this.toast.show({
      severity: "success",
      summary: "Info",
      detail: "Categorie supprimer avec succes!",
      life: 3000,
    });
  };

  creer_cath = () => {
    this.setState({
      create_cat_dialog_active: false,
    });
    // @ts-ignore
    this.toast.show({
      severity: "success",
      summary: "Info",
      detail: "Categorie creer avec succes!",
      life: 3000,
    });
  };

  edit_cath = () => {
    this.setState({
      create_cat_dialog_active: false,
    });
    // @ts-ignore
    this.toast.show({
      severity: "success",
      summary: "Info",
      detail: "Categorie editer avec succes!",
      life: 3000,
    });
  };

  ask_delete_commande = () => {
    this.setState({
      confirm_dialog_active: true,
    });
  };

  hideConfirmDialog = () => {
    this.setState({
      confirm_dialog_active: false,
    });
  };

  hideConfirmDialog_create_cat = () => {
    this.setState({
      create_cat_dialog_active: false,
    });
  };

  renderFooterConfirm() {
    return (
      <div className="footerDialog">
        <div>
          <Button onClick={() => this.delete_cath()}>OUI</Button>
        </div>
        <div>
          <Button
            onClick={() => this.hideConfirmDialog()}
            color="primary"
            variant="contained"
            disableElevation
          >
            NON
          </Button>
        </div>
      </div>
    );
  }

  renderFooter_create_cat() {
    return (
      <div className="footerDialog">
        <div>
          <Button onClick={() => this.hideConfirmDialog_create_cat()}>
            ANNULER
          </Button>
        </div>
        <div>
          {this.state.editing ? (
            <Button
              onClick={() => this.edit_cath()}
              color="primary"
              variant="contained"
              disableElevation
            >
              EDITER
            </Button>
          ) : (
            <Button
              onClick={() => this.creer_cath()}
              color="primary"
              variant="contained"
              disableElevation
            >
              CREER
            </Button>
          )}
        </div>
      </div>
    );
  }

  render() {
    return (
      <div className="view">
        <Toast ref={(el) => (this.toast = el)} position="bottom-left" />
        {/* Suppresion d'une categorie */}
        <Dialog
          header="Confirmation"
          className="detailsPromotionOverlay"
          visible={this.state.confirm_dialog_active}
          footer={this.renderFooterConfirm()}
          onHide={() => this.hideConfirmDialog()}
        >
          <div>Voulez vous vraiment supprimer la categorie</div>
        </Dialog>
        {/* Edition ou ajout d'une categorie */}
        <Dialog
          header={
            this.state.editing ? "Editer une categorie" : "Creer une categorie"
          }
          className="detailsPromotionOverlay"
          visible={this.state.create_cat_dialog_active}
          footer={this.renderFooter_create_cat()}
          onHide={() => this.hideConfirmDialog_create_cat()}
        >
          <div className="create_cat">
            <div className="label_cath">Label de la categorie</div>

            <input
              type="text"
              placeholder="Entrez le label de la categorie"
              defaultValue={this.state.editing ? "Electronique" : ""}
            />
          </div>
        </Dialog>
        <div className="viewTitle">
          <div className="titleContent productsViewTitle">
            <div>
              <h1>Vos Categories</h1>
              <p>Editer, supprimer et creer vos categories ici.</p>
            </div>
            <div className="optionsProductsBut">
              <Button
                variant="outlined"
                onClick={() => this.ask_create_cath()}
                color="primary"
              >
                Ajouter une categorie
              </Button>
            </div>
          </div>
          <div className="filter"></div>
        </div>
        <div className="viewContent cath">
          <div className="productContainer">
            {this.categories.map((cath_name, index) => (
              <div className="cath_item">
                <div className="name"> {cath_name}</div>
                <div className="options">
                  <IconButton
                    aria-label="more"
                    onClick={() => this.ask_edit_cath()}
                  >
                    <Icon>edit</Icon>
                  </IconButton>
                  <IconButton
                    aria-label="more"
                    onClick={() => this.ask_delete_commande()}
                  >
                    <Icon>delete</Icon>
                  </IconButton>
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>
    );
  }
}
