import React, { Component } from "react";
// import "primereact/resources/themes/arya-orange/theme.css";
import "primereact/resources/primereact.css";
import "primeicons/primeicons.css";
import Icon from "@material-ui/core/Icon";
import IconButton from "@material-ui/core/IconButton";
import { Carousel } from "primereact/carousel";
import "./DetailsProducts.css";
import { Rating } from "primereact/rating";
import Emitter from "../../services/emitter";
import { ProgressBar } from "primereact/progressbar";

export default class DetailsProducts extends Component {
  constructor(props) {
    super(props);

    this.state = {
      loadindData: true,
      productImages: [
        {
          id: 0,
          path: "/images/watch.jpg",
        },
        {
          id: 1,
          path: "/images/watch.jpg",
        },
        {
          id: 2,
          path: "/images/watch.jpg",
        },
        {
          id: 3,
          path: "/images/watch.jpg",
        },
        {
          id: 0,
          path: "/images/watch.jpg",
        },
        {
          id: 1,
          path: "/images/watch.jpg",
        },
        {
          id: 2,
          path: "/images/watch.jpg",
        },
        {
          id: 3,
          path: "/images/watch.jpg",
        },
      ],
    };

    this.responsiveOptions = [
      {
        breakpoint: "300px",
        numVisible: 3,
        numScroll: 3,
      },
      {
        breakpoint: "300px",
        numVisible: 2,
        numScroll: 2,
      },
      {
        breakpoint: "300px",
        numVisible: 1,
        numScroll: 1,
      },
    ];
  }

  componentDidMount() {
    var that = this;

    Emitter.on("previewProduct", (idProduct) => {
      console.log("Call the API here " + idProduct);
      that.setState({
        loadindData: true,
      });
      setTimeout(function () {
        that.setState({
          loadindData: false,
        });

        console.log("Stop the shit");
      }, 2000);
    });
  }

  closeDetailsScreen() {
    Emitter.emit("closeDetailsScreen");
  }

  productTemplate(productImage) {
    return (
      <div className="product-item">
        <div className="product-item-content">
          <div className="p-mb-3">
            <img
              src={productImage.path}
              alt={productImage.id}
              className="product-image"
            />
          </div>
        </div>
      </div>
    );
  }

  render() {
    return (
      <div className="detailsProducts">
        <div
          className={
            this.state.loadindData ? "loader active" : "loader disabled"
          }
        >
          <ProgressBar
            mode="indeterminate"
            style={{ height: "4px" }}
          ></ProgressBar>
        </div>
        <div
          className={
            this.state.loadindData ? "loader disabled" : "loader active"
          }
        >
          <div className="headerDetailsProducts">
            <IconButton onClick={this.closeDetailsScreen}>
              <Icon>west</Icon>
            </IconButton>
            <div className="titleDetailsProducts">Apple Watch</div>
          </div>
          <div className="content">
            <div className="images">
              <img src="/images/watch.jpg" alt="" className="shitImage" />
              <div className="extraImages">
                <Carousel
                  value={this.state.productImages}
                  numVisible={3}
                  numScroll={3}
                  responsiveOptions={this.responsiveOptions}
                  itemTemplate={this.productTemplate}
                />
              </div>
            </div>
            <div className="detailsP">
              <h1>Apple Watch - Original release</h1>
              <div className="ratingsReviewsSells">
                <Rating value={4} stars={5} cancel={false} />
                <div className="reviews">125 commentaires</div>
                <div className="orders">
                  <Icon>shopping_cart</Icon>
                  45 ventes
                </div>
              </div>
              <h2>8500 XAF</h2>
              <p className="description">
                Lorem ipsum, dolor sit amet consectetur adipisicing elit. Soluta
                ducimus sequi iure, dolor eum animi asperiores error
                necessitatibus dolore non, at quidem cum! Porro fuga expedita
                vel quo est nostrum nemo error! Unde rem, optio quo, aut,
                delectus minima dolor totam odit omnis cum voluptate.
              </p>
              <div className="features">
                <div className="featureItem">
                  <div className="titleF">Model</div>
                  <div className="valueF">V4500-1457</div>
                </div>
                <div className="featureItem">
                  <div className="titleF">Couleurs disponibles</div>
                  <div className="valueF">Rouge - blanc - orange - bleu</div>
                </div>
                <div className="featureItem">
                  <div className="titleF">Taille disponibles</div>
                  <div className="valueF">LK10 - XL - L45 - M22</div>
                </div>
                <div className="featureItem">
                  <div className="titleF">Categorie</div>
                  <div className="valueF">Chaussures</div>
                </div>
                <div className="featureItem">
                  <div className="titleF">Tags</div>
                  <div className="valueF">
                    chaussures , All star , montre Carousel
                  </div>
                </div>
                <div className="featureItem">
                  <div className="titleF">Stock</div>
                  <div className="valueF">45</div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
