import React, { Component } from "react";
import Product from "../../Components/product/product";
import Pagination from "@material-ui/lab/Pagination";
import "./products.css";
import DetailsProducts from "../detailsProducts/DetailsProducts";
import Emitter from "../../services/emitter";
import Button from "@material-ui/core/Button";
import CreateProduct from "../createProduct/CreateProduct";
import CreatePromotion from "../createPromotion/createPromotion";

export default class Products extends Component {
  loadProducts = (event, value) => {
    console.log("Current Page " + value);
  };

  state = {
    menuActive: false,
    previewProduct: false,
    createPromotion: false,
    previewPromotion: false,
  };

  componentDidMount() {
    Emitter.on("closeDetailsScreen", () => {
      this.setState({
        previewProduct: false,
      });
    });

    Emitter.on("previewProduct", (idProduct) => {
      this.setState({
        previewProduct: true,
      });
    });

    Emitter.on("createPromotion", (idProduct) => {
      this.setState({
        createPromotion: true,
      });
    });

    Emitter.on("closePromotionScreen", (idProduct) => {
      this.setState({
        createPromotion: false,
      });
    });
  }

  createProduct = () => {
    Emitter.emit("createProduct");
  };

  render() {
    return (
      <div className="view">
        <CreateProduct></CreateProduct>

        <div
          className={
            this.state.createPromotion
              ? "DetailsProductsBinder active"
              : "DetailsProductsBinder inactive"
          }
        >
          <CreatePromotion></CreatePromotion>
        </div>
        <div
          className={
            this.state.previewProduct
              ? "DetailsProductsBinder active"
              : "DetailsProductsBinder inactive"
          }
        >
          <DetailsProducts></DetailsProducts>
        </div>

        <div className="viewTitle">
          <div className="titleContent productsViewTitle">
            <div>
              <h1>Vos produits</h1>
              <p>Voir et gerer tous les produits qui sont votre boutique</p>
            </div>
            <div className="optionsProductsBut">
              <Button
                variant="outlined"
                onClick={this.createProduct}
                color="primary"
              >
                Ajouter un produit
              </Button>
            </div>
          </div>
          <div className="filter"></div>
        </div>
        <div className="viewContent">
          <div className="productContainer">
            <Product></Product>
            <Product></Product>
            <Product></Product>
            <Product></Product>
            <Product></Product>
            <Product></Product>
            <Product></Product>
            <Product></Product>
            <Product></Product>
            <Product></Product>
            <Product></Product>
            <Product></Product>
            <Product></Product>
            <Product></Product>
            <Product></Product>
            <Product></Product>
            <Product></Product>
            <Product></Product>
            <Product></Product>
            <Product></Product>
            <Product></Product>
            <Product></Product>
            <Product></Product>
            <Product></Product>
          </div>
          <div className="pagination">
            <Pagination
              count={10}
              variant="outlined"
              onChange={this.loadProducts}
            />
          </div>
        </div>
      </div>
    );
  }
}
