import React, { Component } from "react";
import Icon from "@material-ui/core/Icon";
import IconButton from "@material-ui/core/IconButton";

import Button from "@material-ui/core/Button";
import Emitter from "../../services/emitter";
import { Dialog } from "primereact/dialog";
import { Toast } from "primereact/toast";
import "./commande.css";

export default class Commande extends Component {
  state = {
    screenActive: true,
    displayDetailsPromotion: false,
    showConfirmDialog: false,
  };

  constructor(props) {
    super(props);
    this.reject_commande = this.reject_commande.bind(this);
  }

  hideDialog = () => {
    this.setState({
      displayDetailsPromotion: false,
    });
  };

  showDialog = () => {
    this.setState({
      displayDetailsPromotion: true,
    });
  };

  hideConfirmDialog = () => {
    this.setState({
      showConfirmDialog: false,
    });
  };

  showConfirmDialog = () => {
    this.setState({
      showConfirmDialog: true,
    });
  };

  valider_commande = () => {
    // @ts-ignore
    this.toast.show({
      severity: "success",
      summary: "Info",
      detail: "Commande Valider avec succes!",
      life: 3000,
    });
  };

  reject_commande = () => {
    this.setState({
      showConfirmDialog: false,
    });
    // @ts-ignore
    this.toast.show({
      severity: "success",
      summary: "Info",
      detail: "Commande rejete avec succes!",
      life: 3000,
    });
  };

  renderFooter() {
    return (
      <div className="footerDialog">
        <div>
          <Button
            onClick={() => this.hideDialog()}
            color="primary"
            variant="contained"
            disableElevation
          >
            CONTACTER LE CLIENT
          </Button>
        </div>
        <div>
          <Button onClick={() => this.hideDialog()}>FERMER</Button>
        </div>
      </div>
    );
  }

  renderFooterConfirm() {
    return (
      <div className="footerDialog">
        <div>
          <Button onClick={() => this.reject_commande()}>OUI</Button>
        </div>
        <div>
          <Button
            onClick={() => this.hideConfirmDialog()}
            color="primary"
            variant="contained"
            disableElevation
          >
            NON
          </Button>
        </div>
      </div>
    );
  }

  render() {
    return (
      <div className="view">
        <Toast ref={(el) => (this.toast = el)} position="bottom-left" />
        <Dialog
          header="Confirmation"
          className="detailsPromotionOverlay"
          visible={this.state.showConfirmDialog}
          footer={this.renderFooterConfirm()}
          onHide={() => this.hideConfirmDialog()}
        >
          <div>Voulez vous vraiment rejeter la commande</div>
        </Dialog>
        <Dialog
          header="Details de la Commande"
          className="detailsPromotionOverlay"
          visible={this.state.displayDetailsPromotion}
          footer={this.renderFooter()}
          onHide={() => this.hideDialog()}
        >
          <div className="PreviewPromotionContainer">
            <div className="product">
              <img
                src="/images/watch.jpg"
                alt=""
                className="promotionImage_details"
              />
              <div>Adidas premium</div>
            </div>
            <div className="details">
              <div className="detailsItem">
                <div>Nom du client</div>
                <div>Afrographix Studio</div>
              </div>
              <div className="detailsItem">
                <div>Numero de telephone</div>
                <div>697 452 145</div>
              </div>
              <div className="detailsItem">
                <div>Quantite commande </div>
                <div>45</div>
              </div>
              <div className="detailsItem">
                <div>Couleur</div>
                <div>Jaune</div>
              </div>
              <div className="detailsItem">
                <div>Date de la commande</div>
                <div>14 fevrier 2021</div>
              </div>
            </div>
          </div>
        </Dialog>
        <h1 className="titleView">Commandes (04)</h1>

        <div className="content">
          <div className="detailsP">
            <h2>
              Consulter les Commandes actuellement <br />
              En cours Dans votre boutique
            </h2>
            <p>Ici vous pouvez Valider ou rejeter une commande</p>
            <div className="promotionContainer">
              <div className="commandeItem">
                <div className="detailsCommmandeSmall">
                  <div>Adidas Dernier cris</div>
                  <div className="quantity">Quantite : 05</div>
                </div>

                <div className="actions">
                  <Button
                    variant="contained"
                    disableElevation
                    color="primary"
                    onClick={() => this.valider_commande()}
                  >
                    <Icon>done</Icon>
                    Valider
                  </Button>
                  <Button
                    variant="contained"
                    disableElevation
                    color="primary"
                    onClick={() => this.showConfirmDialog()}
                  >
                    <Icon>trending_up</Icon>
                    Rejeter
                  </Button>

                  <IconButton onClick={() => this.showDialog()}>
                    <Icon>more_vert</Icon>
                  </IconButton>
                </div>
              </div>
              <div className="commandeItem">
                <div className="detailsCommmandeSmall">
                  <div>Adidas Dernier cris</div>
                  <div className="quantity">Quantite : 05</div>
                </div>

                <div className="actions">
                  <Button variant="contained" disableElevation color="primary">
                    <Icon>done</Icon>
                    Valider
                  </Button>
                  <Button variant="contained" disableElevation color="primary">
                    <Icon>trending_up</Icon>
                    Rejeter
                  </Button>

                  <IconButton onClick={() => this.showDialog()}>
                    <Icon>more_vert</Icon>
                  </IconButton>
                </div>
              </div>
              <div className="commandeItem">
                <div className="detailsCommmandeSmall">
                  <div>Adidas Dernier cris</div>
                  <div className="quantity">Quantite : 05</div>
                </div>

                <div className="actions">
                  <Button variant="contained" disableElevation color="primary">
                    <Icon>done</Icon>
                    Valider
                  </Button>
                  <Button variant="contained" disableElevation color="primary">
                    <Icon>trending_up</Icon>
                    Rejeter
                  </Button>

                  <IconButton onClick={() => this.showDialog()}>
                    <Icon>more_vert</Icon>
                  </IconButton>
                </div>
              </div>
              <div className="commandeItem">
                <div className="detailsCommmandeSmall">
                  <div>Adidas Dernier cris</div>
                  <div className="quantity">Quantite : 05</div>
                </div>

                <div className="actions">
                  <Button variant="contained" disableElevation color="primary">
                    <Icon>done</Icon>
                    Valider
                  </Button>
                  <Button variant="contained" disableElevation color="primary">
                    <Icon>trending_up</Icon>
                    Rejeter
                  </Button>

                  <IconButton onClick={() => this.showDialog()}>
                    <Icon>more_vert</Icon>
                  </IconButton>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
