import React, { Component } from "react";

import Button from "@material-ui/core/Button";
import "./settings.css";

export default class Settings extends Component {
  state = {
    initialLogo: "/images/lefa.jpg",
    newLogo: null,
  };

  loadImage = (event) => {
    let logoFile = event.target.files[0];
    // render the image on the UI
    var reader = new FileReader();
    var imageExtension = ["jpg", "png", "gif", "jpeg"];
    var extension = logoFile.name.split(".")[1].toLowerCase();

    var that = this;

    reader.onload = function () {
      if (imageExtension.indexOf(extension) !== -1) {
        //update the state
        that.setState({
          newLogo: logoFile,
          initialLogo: this.result,
        });
      } else {
        alert("unsupported image file!");
      }
    };

    reader.readAsDataURL(logoFile);
  };

  clickHiddenInput = () => {
    // @ts-ignore
    document.querySelector("#logo").click();
  };

  render() {
    return (
      <div className="view settingsView">
        <h1>Parametres de votre boutique</h1>

        <p className="titleMessageSettings">
          Ici vous pouvez editer les informations de votre relatives a votre
          boutique. renseigner les nouvelles informations et cliquer sur editer
          pour appliquer les modifications
        </p>
        <div className="accountSettingsWrapper">
          <div className="settingsItem logo">
            <div className="label">Logo de la boutique</div>
            <div className="inputArea">
              <input type="file" hidden id="logo" onChange={this.loadImage} />
              <img
                src={this.state.initialLogo}
                alt=""
                onClick={this.clickHiddenInput}
              />
            </div>
          </div>
          <div className="settingsItem">
            <div className="label">Nom de la boutique</div>
            <div className="inputArea">
              <input
                type="text"
                defaultValue="AfrographixShop"
                id="fname"
                name="fname"
              />
            </div>
          </div>
          <div className="settingsItem">
            <div className="label">Nom du personnel</div>
            <div className="inputArea">
              <input type="text" defaultValue="Afrographix" />
            </div>
          </div>

          <div className="settingsItem">
            <div className="label">Sexe</div>
            <div className="inputArea">
              <select name="sexe" id="sexe">
                <option value="Masculin">Masculin</option>
                <option value="Feminin">Feminin</option>
              </select>
            </div>
          </div>

          <div className="settingsItem">
            <div className="label">Date de naissance</div>
            <div className="inputArea">
              <input type="date" defaultValue="1998-08-14" />
            </div>
          </div>

          <div className="settingsItem">
            <div className="label">Email</div>
            <div className="inputArea">
              <input type="email" defaultValue="Afrographix@king.com" />
            </div>
          </div>
          <div className="settingsItem">
            <div className="label">Telephone</div>
            <div className="inputArea">
              <input type="text" defaultValue="697521012" />
            </div>
          </div>
          <div className="settingsItem">
            <div className="label">Adresse</div>
            <div className="inputArea">
              <input type="text" defaultValue="Douala - Ndonbong" />
            </div>
          </div>
          <div className="settingsItem">
            <div className="label">Type de produit</div>
            <div className="inputArea">
              <input type="text" defaultValue="Habillement" />
            </div>
          </div>
          <div className="settingsItem">
            <div className="label">Type de commerce</div>
            <div className="inputArea">
              <select name="sexe" id="sexe">
                <option value="Masculin">Grossiste</option>
                <option value="Feminin">Detaillant</option>
              </select>
            </div>
          </div>

          <div className="footerUI">
            <Button variant="contained" color="primary" disableElevation>
              Editer
            </Button>
          </div>
        </div>
      </div>
    );
  }
}
