import React, { Component } from "react";

import Icon from "@material-ui/core/Icon";
import IconButton from "@material-ui/core/IconButton";
import { Carousel } from "primereact/carousel";
import Emitter from "../../services/emitter";
import { ProgressBar } from "primereact/progressbar";
import Button from "@material-ui/core/Button";

import "primeicons/primeicons.css";
import "primereact/resources/themes/saga-blue/theme.css";
import "primereact/resources/primereact.css";

import { Toast } from "primereact/toast";

import TextField from "@material-ui/core/TextField";
import MenuItem from "@material-ui/core/MenuItem";

import "./createProduct.css";

export default class CreateProduct extends Component {
  state = {
    editing: false,
    categorie: "",
    productImagesFiles: [],
    createProductActive: false,
    productCreationLoading: false,
    productImages: [],
  };
  constructor(props) {
    super(props);

    this.responsiveOptions = [
      {
        breakpoint: "300px",
        numVisible: 3,
        numScroll: 3,
      },
      {
        breakpoint: "300px",
        numVisible: 2,
        numScroll: 2,
      },
      {
        breakpoint: "300px",
        numVisible: 1,
        numScroll: 1,
      },
    ];
  }
  componentDidMount() {
    var that = this;

    Emitter.on("createProduct", () => {
      console.log("handle this garbage the create product screen!");
      that.setState({
        createProductActive: true,
      });
    });

    Emitter.on("editProduct", () => {
      console.log("Edit this product");
      that.setState({
        editing: true,
        createProductActive: true,
      });
    });
  }

  closeCreateProductScreen = () => {
    console.log("Close the create product screen!");
    this.setState({
      createProductActive: false,
      editing: false,
    });
  };
  deleteImage = () => {
    console.log("Remove the image");
  };
  productTemplate(productImage, deleteFunction) {
    return (
      <div className="product-item">
        <img
          src={productImage.path}
          alt={productImage.id}
          className="product-image"
        />
      </div>
    );
  }

  categories = [
    {
      value: "Lorem 1",
      label: "Lorem 1",
    },
    {
      value: "Lorem 2",
      label: "Lorem 2",
    },
    {
      value: "Lorem 3",
      label: "Lorem 3",
    },
    {
      value: "Lorem 4",
      label: "Lorem 4",
    },
  ];

  handleChange = (event) => {
    this.setState({
      categorie: event.target.value,
    });
  };

  clickHiddenInput = () => {
    // @ts-ignore
    document.querySelector("#hiddenInput").click();
  };

  loadImage = (event) => {
    let productImageFile = event.target.files[0];
    this.setState({
      productImagesFiles: this.state.productImagesFiles.concat([
        productImageFile,
      ]),
    });

    // render the image on the UI
    var tempPathImagePreview;
    var reader = new FileReader();
    var imageExtension = ["jpg", "png", "gif", "jpeg"];
    var extension = productImageFile.name.split(".")[1].toLowerCase();

    var that = this;

    reader.onload = function () {
      if (imageExtension.indexOf(extension) !== -1) {
        tempPathImagePreview = this.result;

        //update the state
        that.setState({
          productImages: [
            {
              id: 0,
              path: tempPathImagePreview,
            },
          ].concat(that.state.productImages),
        });
      } else {
        alert("unsupported image file!");
      }
    };

    reader.readAsDataURL(productImageFile);
  };

  createProduct = () => {
    console.log("Creation loading...");
    this.setState({
      productCreationLoading: true,
    });

    let that = this;

    // API Call
    setTimeout(function () {
      that.setState({
        productCreationLoading: false,
      });

      that.showSuccess(that);

      setTimeout(function () {
        that.closeCreateProductScreen();
      }, 2000);
    }, 3000);
  };

  showSuccess(that) {
    // @ts-ignore

    if (!this.state.editing) {
      that.toast.show({
        severity: "success",
        summary: "Info",
        detail: "Produit Creer  avec succes!",
        life: 3000,
      });
    } else {
      that.toast.show({
        severity: "success",
        summary: "Info",
        detail: "Produit Modifier  avec succes!",
        life: 3000,
      });
    }
  }

  render() {
    return (
      <div
        className={
          !this.state.createProductActive
            ? "createProductScreen disabled"
            : "createProductScreen active"
        }
      >
        <div
          className={
            this.state.productCreationLoading
              ? "loaderCreateProduct active"
              : "loaderCreateProduct disabled"
          }
        >
          <ProgressBar
            color="red"
            mode="indeterminate"
            style={{ height: "4px" }}
          ></ProgressBar>
        </div>
        <div>
          <Toast ref={(el) => (this.toast = el)} position="bottom-center" />
          <div className="headerDetailsProducts createProduct">
            <IconButton onClick={this.closeCreateProductScreen}>
              <Icon>close</Icon>
            </IconButton>
          </div>
          <div className="content">
            <div className="">
              <div
                className=" loadImageTrigger"
                onClick={this.clickHiddenInput}
              >
                <input
                  type="file"
                  hidden
                  onChange={this.loadImage}
                  id="hiddenInput"
                />
                <img src="/images/add-image.png" alt="" />
                <p>Cliquez pour ajouter une image</p>
              </div>
              <div className="totalImages">
                {this.state.productImagesFiles.length} image(s)
              </div>
              <div className="extraImages">
                <Carousel
                  value={this.state.productImages}
                  numVisible={3}
                  numScroll={3}
                  responsiveOptions={this.responsiveOptions}
                  itemTemplate={this.productTemplate}
                />
              </div>
            </div>
            <div className="detailsP overlayDetailsP">
              {!this.state.editing ? (
                <div>
                  <h1>Creation d'un produit</h1>
                  <p>
                    Veuillez renseigner les details concernant le produit <br />
                    que vous souhaitez enregistrer
                  </p>
                </div>
              ) : (
                <div>
                  <h1>Edition d'un produit</h1>
                  <p>
                    Veuillez renseigner les nouvelles informations <br />
                    concernant le produit
                  </p>
                </div>
              )}

              <div className="featuresInput">
                <TextField
                  id="outlined-basic"
                  label="Nom du produit"
                  variant="outlined"
                  error={true}
                  helperText="Valeur incorrect"
                />
                <TextField
                  id="outlined-basic"
                  label="Prix"
                  variant="outlined"
                  type="number"
                />
                <TextField
                  id="outlined-multiline-static"
                  label="Descrption du produit"
                  multiline
                  rows={4}
                  variant="outlined"
                  className="descriptionProduct"
                />
                <TextField
                  id="outlined-basic"
                  label="Stock"
                  variant="outlined"
                  type="number"
                />
                <TextField
                  id="outlined-basic"
                  label="Tags"
                  defaultValue={this.state.editing ? " Chaussure, lorem14" : ""}
                  variant="outlined"
                  type="text"
                />
                <TextField
                  id="outlined-select-currency"
                  select
                  label="Cathegorie"
                  value={this.state.categorie}
                  onChange={this.handleChange}
                  helperText=""
                  variant="outlined"
                >
                  {this.categories.map((option) => (
                    <MenuItem key={option.value} value={option.value}>
                      {option.label}
                    </MenuItem>
                  ))}
                </TextField>
              </div>

              <div className="footerUI">
                <Button
                  variant="contained"
                  disableElevation
                  onClick={this.closeCreateProductScreen}
                >
                  Annuler
                </Button>
                <Button
                  variant="contained"
                  color="primary"
                  disableElevation
                  onClick={this.createProduct}
                >
                  {!this.state.editing ? (
                    <span>CREER</span>
                  ) : (
                    <span>MODIFIER</span>
                  )}
                </Button>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
