import React, { Component } from "react";
import Divider from "@material-ui/core/Divider";
import Icon from "@material-ui/core/Icon";
import MoreVertIcon from "@material-ui/icons/MoreVert";
import IconButton from "@material-ui/core/IconButton";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import Button from "@material-ui/core/Button";

import { ProgressSpinner } from "primereact/progressspinner";

import "primeicons/primeicons.css";
import "primereact/resources/themes/saga-blue/theme.css";
import "primereact/resources/primereact.css";

import { Toast } from "primereact/toast";

import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";

import "./product.css";
import Emitter from "../../services/emitter";

export default class Product extends Component {
  state = {
    menuActive: false,
    dialogOpen: false,
    deletionOngoing: false,
  };

  togglemenu = () => {
    this.setState({
      menuActive: !this.state.menuActive,
    });
  };

  preview(idProduct) {
    console.log("Preview Items");

    Emitter.emit("previewProduct", idProduct);

    this.setState({
      menuActive: false,
    });
  }
  edit = (idProduct) => {
    console.log("Edit Items");
    this.setState({
      menuActive: false,
    });

    Emitter.emit("editProduct", idProduct);
  };
  promote = (idProduct) => {
    Emitter.emit("createPromotion", idProduct);
    this.setState({
      menuActive: false,
    });
  };

  // Delete functions
  delete = () => {
    console.log("delete Items");
    this.setState({
      menuActive: false,
      dialogOpen: true,
    });
  };

  deleteItem = () => {
    this.setState({
      deletionOngoing: true,
    });

    var that = this;
    setTimeout(function () {
      console.log("Suppression imminente");
      that.showSuccess();
      that.setState({
        dialogOpen: false,
        deletionOngoing: false,
      });
    }, 2000);
  };

  handleClose = () => {
    console.log("Wouala");
    this.setState({
      dialogOpen: false,
    });
  };

  constructor(props) {
    super(props);
    this.showSuccess = this.showSuccess.bind(this);
  }

  showSuccess() {
    // @ts-ignore
    this.toast.show({
      severity: "success",
      summary: "Info",
      detail: "Produit supprimer avec succes!",
      life: 3000,
    });
  }

  render() {
    return (
      <div className="productItem">
        <Toast ref={(el) => (this.toast = el)} position="bottom-left" />
        {/* Confirm dialog */}
        <Dialog open={this.state.dialogOpen} onClose={this.handleClose}>
          <DialogTitle style={{ cursor: "move" }}>
            {!this.state.deletionOngoing ? (
              <div>Confirmation</div>
            ) : (
              <div></div>
            )}
          </DialogTitle>
          <DialogContent>
            <DialogContentText>
              {this.state.deletionOngoing ? (
                <div className="loaderP">
                  <ProgressSpinner
                    style={{ width: "50px", height: "50px" }}
                    strokeWidth="4"
                    fill="#EEEEEE"
                    animationDuration=".5s"
                  />
                </div>
              ) : (
                <div> Voulez vous vraiment supprimer le produit?</div>
              )}
            </DialogContentText>
          </DialogContent>
          <DialogActions>
            {!this.state.deletionOngoing ? (
              <div>
                <Button autoFocus onClick={this.handleClose}>
                  NON
                </Button>
                <Button onClick={this.deleteItem}>OUI</Button>
              </div>
            ) : (
              <div className="deletionOngoing">Supression en cours...</div>
            )}
          </DialogActions>
        </Dialog>
        {/* Dialog box */}

        <div className="menuButton">
          <IconButton aria-label="more" onClick={this.togglemenu}>
            {this.state.menuActive ? <Icon>close</Icon> : <MoreVertIcon />}
          </IconButton>
        </div>
        <div
          className={this.state.menuActive ? "menu active" : "menu disabled"}
        >
          <List component="nav" aria-label="main mailbox folders">
            <ListItem button onClick={() => this.preview(11)}>
              <ListItemIcon>
                <Icon>preview</Icon>
              </ListItemIcon>
              <ListItemText primary="Visualiser" />
            </ListItem>
            <ListItem button onClick={() => this.edit(11)}>
              <ListItemIcon>
                <Icon>edit</Icon>
              </ListItemIcon>
              <ListItemText primary="Editer" />
            </ListItem>
            <ListItem button onClick={() => this.promote(11)}>
              <ListItemIcon>
                <Icon>shopping_cart</Icon>
              </ListItemIcon>
              <ListItemText primary="Creer une promotion" />
            </ListItem>
            <ListItem button onClick={this.delete}>
              <ListItemIcon>
                <Icon>delete</Icon>
              </ListItemIcon>
              <ListItemText primary="Supprimer" />
            </ListItem>
          </List>
        </div>
        <div className="imageProduct">
          <img src="/images/watch.jpg" alt="" />
        </div>
        <Divider />
        <br />
        <div className="productDetails">
          <div>Apple Watch 325 revolution</div>
          <div className="namePrice">
            <h4>4500 XAF</h4>
            <div className="stock">Stock : 4</div>
          </div>
        </div>
      </div>
    );
  }
}
