import React, { Component } from "react";

import TextField from "@material-ui/core/TextField";
import MenuItem from "@material-ui/core/MenuItem";
import { Chart } from "primereact/chart";
import "./statistique.css";

export default class Statistique extends Component {
  state = {
    selectedYear: 2021,
    selectedMonth: "janvier",
  };

  constructor(props) {
    super(props);

    this.basicData = {
      labels: [
        "Janvier",
        "Fevrier",
        "Mars",
        "Avril",
        "Mai",
        "Juin",
        "Juillet",
        "Aout",
        "Septembre",
        "Octobre",
        "Novembre",
        "Decembre",
      ],
      datasets: [
        {
          label: "Ventes",
          data: [65, 59, 80, 81, 56, 55, 40, 11, 44, 10, 44, 15],
          fill: false,
          borderColor: "#f79a33",
          tension: 0.4,
        },
      ],
    };
  }

  handleChangeAnnee = (event) => {
    this.setState({
      selectedYear: event.target.value,
    });
  };

  handleChangeMois = (event) => {
    this.setState({
      selectedMonth: event.target.value,
    });
  };

  annees = ["2018", "2019", "2020", "2021"];

  render() {
    return (
      <div className="view">
        <div className="statContainer ">
          <h1>Statistiques</h1>
          <p className="titleMessageSettings">
            Suivez l'evolution de vos ventes
          </p>
          <hr />
          <div className="ventesRevenu">
            <div className="big ventes">
              <div>14 821 458</div>
              <div>ventes</div>
            </div>
            <div className="big revenu">
              <div>108 000 000</div>
              <div>Revenu en FCFA</div>
            </div>
          </div>
          <hr />
          <div className="evolutionVente">
            <p className="titleStat">Evolutions de vos ventes</p>
            {/* <div className="filters">
              <select name="annee" id="annee">
                <option value="2021">2021</option>
                <option value="2020">2020</option>
                <option value="2020">2019</option>
              </select>
            </div> */}
          </div>
          <div className="card">
            <Chart type="line" data={this.basicData} />
          </div>
        </div>
      </div>
    );
  }
}
