import { createTheme, ThemeProvider } from "@material-ui/core";
import React, { Component } from "react";
import Button from "@material-ui/core/Button";
import Search from "@material-ui/icons/Search";
import Avatar from "@material-ui/core/Avatar";

import Icon from "@material-ui/core/Icon";
import IconButton from "@material-ui/core/IconButton";

import Statistique from "./Vues/statistique/statistique";

import "./App.css";
import Products from "./Vues/products/Products";
import Settings from "./Vues/settings/settings";

import Emitter from "services/emitter";
import Promotion from "./Vues/promotion/promotion";
import Commande from "./Vues/commandes/commande";
import Categorie from "./Vues/categorie/categorie";

const theme = createTheme({
  palette: {
    primary: {
      main: "#F47106",
    },
    secondary: {
      main: "#F79300",
    },
  },
  typography: {
    fontFamily: "googleFont",
  },
});

class App extends Component {
  componentDidMount() {
    console.log("component mounted");
  }

  componentWillUnmount() {
    console.log("Component unmounted");
  }

  state = {
    productsActive: true,
    statsActive: false,
    navActive: false,
    settingsActive: false,
    promotionActive: false,
    commandeActive: false,
    categorieActive: false,
  };

  disableButtonState = () => {
    this.setState({
      productsActive: false,
      statsActive: false,
      navActive: false,
      settingsActive: false,
      promotionActive: false,
      commandeActive: false,
      categorieActive: false,
    });
  };

  load_promotion = () => {
    this.disableButtonState();
    this.setState({
      promotionActive: true,
    });
  };
  load_commande = () => {
    this.disableButtonState();
    this.setState({
      commandeActive: true,
    });
  };

  load_categorie = () => {
    this.disableButtonState();
    this.setState({
      categorieActive: true,
    });
  };

  loadProducts = () => {
    this.disableButtonState();
    this.setState({
      productsActive: true,
    });
  };

  loadStats = () => {
    this.disableButtonState();
    this.setState({
      statsActive: true,
    });
  };

  loadSettings = () => {
    this.disableButtonState();
    this.setState({
      settingsActive: true,
    });
  };

  showNavbar = () => {
    this.setState({
      navActive: true,
    });
  };

  hideNavbar = () => {
    this.setState({
      navActive: false,
    });
  };

  render() {
    return (
      <ThemeProvider theme={theme}>
        <div className="App">
          <div
            className={
              this.state.navActive ? "sideBar active" : "sideBar disabled"
            }
          >
            <div className="title">
              <div>
                <div className="logoSymbole">C</div>
                <div className="name">DashBoard</div>
              </div>
              <IconButton
                onClick={() => this.hideNavbar()}
                className="toggleNavbar"
              >
                <Icon>close</Icon>
              </IconButton>
            </div>
            <div className="navigationLink">
              <div
                className={
                  this.state.productsActive ? "linkItem active" : "linkItem "
                }
                onClick={() => this.loadProducts()}
              >
                <img
                  src={require("./assets/images/produits.png").default}
                  alt=""
                  className="productIcon"
                />
                <div className="labelItem">Produits</div>
              </div>
              <div
                className={
                  this.state.categorieActive ? "linkItem active" : "linkItem "
                }
                onClick={() => this.load_categorie()}
              >
                <img src="/images/SVG/cat.svg" alt="" className="productIcon" />
                <div className="labelItem">Categories</div>
              </div>
              <div
                className={
                  this.state.commandeActive ? "linkItem active" : "linkItem "
                }
                onClick={() => this.load_commande()}
              >
                <img
                  src="/images/delivery.png"
                  alt=""
                  className="productIcon"
                />
                <div className="labelItem">Commande</div>
              </div>
              <div
                className={
                  this.state.promotionActive ? "linkItem active" : "linkItem "
                }
                onClick={() => this.load_promotion()}
              >
                <img
                  src="/images/promotionIcon.png"
                  alt=""
                  className="productIcon"
                />
                <div className="labelItem">Promotion</div>
              </div>
              <div
                className={
                  this.state.statsActive ? "linkItem active" : "linkItem "
                }
                onClick={() => this.loadStats()}
              >
                <img
                  src={require("./assets/images/stats.png").default}
                  alt=""
                />
                <div className="labelItem">Statistiques</div>
              </div>
            </div>
            <div className="settingsLink">
              <div
                className={
                  this.state.settingsActive ? "linkItem active" : "linkItem "
                }
                onClick={() => this.loadSettings()}
              >
                <img
                  src={require("./assets/images/settings.png").default}
                  alt=""
                />
                <div className="labelItem">Parametres</div>
              </div>
            </div>
          </div>
          <div className="mainApp">
            <div className="header">
              <IconButton
                onClick={() => this.showNavbar()}
                className="toggleNavbar"
              >
                <Icon>menu</Icon>
              </IconButton>
              <div className="searchArea">
                <Search></Search>
                <input type="text" placeholder="Entrez le nom du produit..." />
              </div>
              <div className="ThemeContainer">
                <div className="themeIndicator">
                  {/* <img
                    src={require("./assets/images/moon.png").default}
                    alt=""
                  /> */}

                  <img
                    src={require("./assets/images/light.png").default}
                    alt=""
                  />
                </div>
              </div>
              <Avatar
                alt="Cindy Baker"
                src={require("./assets/images/lefa.jpg").default}
              />
            </div>
            <div className="routerArea">
              {/* Statistique */}
              {this.state.statsActive ? (
                <Statistique></Statistique>
              ) : (
                <div></div>
              )}
              {/* Produits */}
              {this.state.productsActive ? <Products></Products> : <div></div>}
              {/* Parametres */}
              {this.state.settingsActive ? <Settings></Settings> : <div></div>}
              {this.state.promotionActive ? (
                <Promotion></Promotion>
              ) : (
                <div></div>
              )}
              {this.state.commandeActive ? <Commande></Commande> : <div></div>}
              {this.state.categorieActive ? (
                <Categorie></Categorie>
              ) : (
                <div></div>
              )}
            </div>
          </div>
        </div>
      </ThemeProvider>
    );
  }
}

export default App;
