// @ts-nocheck
import React, { Component } from "react";
// import "primereact/resources/themes/arya-orange/theme.css";
import "primereact/resources/primereact.css";
import "primeicons/primeicons.css";
import Icon from "@material-ui/core/Icon";
import IconButton from "@material-ui/core/IconButton";
import Button from "@material-ui/core/Button";
import { Toast } from "primereact/toast";
import Emitter from "../../services/emitter";
import TextField from "@material-ui/core/TextField";
import { ProgressSpinner } from "primereact/progressspinner";
import InputAdornment from "@material-ui/core/InputAdornment";
import "./createPromotion.css";

export default class CreatePromotion extends Component {
  state = {
    screenActive: false,
    createPromoButtonActive: false,
    loading: false,
  };

  constructor(props) {
    super(props);
    this.launchPromotionLater = this.launchPromotionLater.bind(this);
    this.launchPromotionNow = this.launchPromotionNow.bind(this);
  }

  componentDidMount() {
    var that = this;

    Emitter.on("createPromotion", (idProduct) => {
      console.log("Create this promotion");
      that.setState({
        screenActive: true,
      });
    });
  }

  closePromotionScreen() {
    Emitter.emit("closePromotionScreen");
  }

  launchPromotionNow() {
    this.toast.show({
      severity: "success",
      summary: "Info",
      detail: "Promotion Lance!",
      life: 3000,
    });

    setTimeout(function () {
      Emitter.emit("closePromotionScreen");
    }, 2000);
  }

  launchPromotionLater() {
    this.toast.show({
      severity: "success",
      summary: "Info",
      detail: "Promotion Enregistrer!",
      life: 3000,
    });

    setTimeout(function () {
      Emitter.emit("closePromotionScreen");
    }, 2000);
  }

  activateTheCreateButton = () => {
    this.setState({
      createPromoButtonActive: true,
    });
  };

  render() {
    return (
      <div className="detailsProducts">
        <div
          className={
            this.state.screenActive ? "loader active " : "loader disabled"
          }
        >
          <Toast ref={(el) => (this.toast = el)} position="bottom-center" />
          <div className="headerDetailsProducts">
            <IconButton onClick={this.closePromotionScreen}>
              <Icon>west</Icon>
            </IconButton>
            <div className="titleDetailsProducts">Back</div>
          </div>
          <div className="content">
            <div className="images">
              <img src="/images/watch.jpg" alt="" className="shitImage" />
            </div>
            <div className="detailsP overlayDetailsP">
              <h1>
                Creation d'une promotion sur <br />
                <span className="nameProductPromotion">
                  Apple Watch - Original release
                </span>
              </h1>
              <p>Veuillez renseigner les informations de la promotion</p>

              <div className="featuresInput">
                <TextField
                  id="outlined-basic"
                  label="Ancien prix"
                  variant="outlined"
                  type="number"
                  defaultValue="57100"
                  InputProps={{
                    readOnly: true,
                  }}
                />
                <TextField
                  id="outlined-basic"
                  label="Nouveau prix"
                  variant="outlined"
                  type="number"
                />
              </div>

              <div className="selectDate">
                <div>Choississez la date de fin</div>
                <input type="date" />
              </div>

              <div className="promotionTimeConfirmation">
                <p>Voulez vous lancez la promotion maintenant?</p>
                <div className="validationButtons">
                  <Button
                    variant="contained"
                    disableElevation
                    onClick={this.launchPromotionNow}
                  >
                    OUI
                  </Button>

                  <Button
                    variant="contained"
                    disableElevation
                    onClick={this.activateTheCreateButton}
                  >
                    NON
                  </Button>
                </div>
              </div>

              {this.state.createPromoButtonActive ? (
                <div className="selectDate">
                  <div>Choississez la date de lancement</div>
                  <input type="date" />
                </div>
              ) : (
                <div></div>
              )}

              <div className="footerCreateProduct">
                <Button
                  variant="contained"
                  disableElevation
                  onClick={this.closePromotionScreen}
                >
                  Annuler
                </Button>
                <div>
                  <Button
                    variant="contained"
                    color="primary"
                    disableElevation
                    onClick={this.launchPromotionLater}
                  >
                    <span>CREER</span>
                  </Button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
