import React, { Component } from "react";
import Icon from "@material-ui/core/Icon";
import IconButton from "@material-ui/core/IconButton";

import Button from "@material-ui/core/Button";
import Emitter from "../../services/emitter";
import { Dialog } from "primereact/dialog";
import "./promotion.css";

export default class Promotion extends Component {
  state = {
    screenActive: true,
    displayDetailsPromotion: false,
  };

  componentDidMount() {
    var that = this;

    Emitter.on("previewPromotion", () => {
      console.log("Preview this damn Screen");
      that.setState({
        screenActive: true,
      });
    });
  }

  closePromotionPreviewScreen = () => {
    this.setState({
      screenActive: false,
    });
  };

  hideDialog = () => {
    this.setState({
      displayDetailsPromotion: false,
    });
  };

  showDialog = () => {
    this.setState({
      displayDetailsPromotion: true,
    });
  };

  renderFooter() {
    return (
      <div>
        <Button onClick={() => this.hideDialog()}>OK</Button>
      </div>
    );
  }

  render() {
    return (
      <div className="view">
        <Dialog
          header="Details de la promotion"
          className="detailsPromotionOverlay"
          visible={this.state.displayDetailsPromotion}
          footer={this.renderFooter()}
          onHide={() => this.hideDialog()}
        >
          <div className="PreviewPromotionContainer">
            <div className="product">
              <img
                src="/images/watch.jpg"
                alt=""
                className="promotionImage_details"
              />
              <div>Adidas premium</div>
            </div>
            <div className="details">
              <div className="detailsItem">
                <div>Prix initiale</div>
                <div>8700 FCA</div>
              </div>
              <div className="detailsItem">
                <div>Prix Promotionelle</div>
                <div>4500 FCA</div>
              </div>
              <div className="detailsItem">
                <div>Date de debut </div>
                <div>12 janvier 2021</div>
              </div>
              <div className="detailsItem">
                <div>Date de fin</div>
                <div>14 fevrier 2021</div>
              </div>
              <div className="detailsItem">
                <div>Jours restants</div>
                <div>45 jours</div>
              </div>
            </div>
          </div>
        </Dialog>
        <h1 className="titleView">Promotion</h1>
        <hr />
        <div className="content">
          <div className="detailsP">
            <h2>
              Consulter les produits actuellement <br />
              En promotion
            </h2>
            <p>Ici vous pouvez annuler une promotion.</p>
            <div className="promotionContainer">
              <div className="promotionItem">
                <div>Adidas XL 145</div>

                <div>
                  <Button variant="contained" disableElevation>
                    ARRETER
                  </Button>

                  <IconButton onClick={() => this.showDialog()}>
                    <Icon>more_vert</Icon>
                  </IconButton>
                </div>
              </div>
              <div className="promotionItem">
                <div>Adidas XL 145</div>

                <div>
                  <Button variant="contained" disableElevation>
                    ARRETER
                  </Button>

                  <IconButton onClick={() => this.showDialog()}>
                    <Icon>more_vert</Icon>
                  </IconButton>
                </div>
              </div>
              <div className="promotionItem">
                <div>Adidas XL 145</div>

                <div>
                  <Button variant="contained" disableElevation>
                    ARRETER
                  </Button>

                  <IconButton onClick={() => this.showDialog()}>
                    <Icon>more_vert</Icon>
                  </IconButton>
                </div>
              </div>
              <div className="promotionItem">
                <div>Adidas XL 145</div>

                <div>
                  <Button variant="contained" disableElevation>
                    ARRETER
                  </Button>

                  <IconButton onClick={() => this.showDialog()}>
                    <Icon>more_vert</Icon>
                  </IconButton>
                </div>
              </div>
            </div>
          </div>
          {/* <div className="images">
            <img
              src="/images/promotion.png"
              alt=""
              className="shitImage promotionImage"
            />
          </div> */}
        </div>
      </div>
    );
  }
}
